$color-main: #0056A9;

body {
    background: black; }

.login-section {
    min-height: 740px;
    background: url("../images/192.png"), $color-main 50%;
    background-repeat: no-repeat;
    background-blend-mode: screen; }

.col-lg-5 {
    flex: 0 0 auto;
    width: 54.666667%; }

.login-container {
    &__content {
        width: 810px;
        margin: 0 auto;
        margin-top: 100px; } }


.card-header {
    font-size: 28px;
    background-color: #dddddd; }

.form-error {
    color: #d52e2e; }

.py-4 {
    padding-top: 1rem!important;
    padding-bottom: 1rem!important; }

